.copyright {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0;

  @media (min-width: 768px) {
    justify-content: end;
  }
}

.copyright__text {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: var(--special_dark-grey);
  text-decoration: none;

  @media (min-width: 768px) {
    margin-right: 22px;
  }
}
