.additional {
  background-color: var(--basic-white);
}

.additional__wrapper {
  padding: 0 20px;

  @media (min-width: 768px) {
    padding: 0 30px;
  }

  @media (min-width: 1440px) {
    padding: 0;
  }
}

.additional__title {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  color: var(--basic-black);
  margin: 0;
  margin-bottom: 36px;

  @media (min-width: 768px) {
    font-size: 36px;
    line-height: 36px;
  }

  @media (min-width: 1440px) {
    margin-bottom: 80px;
  }
}

.additional__list {
  list-style-type: none;
  padding: 0;

  @media (min-width: 786px) {
    // padding: 0 38px;
    border: 1px solid var(--special-light-grey-2);
  }

  @media (min-width: 1440px) {
    border: none;
  }
}

.additional__item {
  display: grid;
  grid-template-rows: repeat(3, auto);
  padding-top: 13px;
  padding-bottom: 21px;
  border-top: 1px solid var(--special-light-grey-2);

  &:last-child {
    border-bottom: 1px solid var(--special-light-grey-2);
  }

  @media (min-width: 768px) {
    grid-template-rows: repeat(2, auto);
    grid-template-columns: repeat(2, auto);

    &:first-child {
      border-top: none;
    }
  }

  @media (min-width: 1440px) {
    grid-template-rows: 1fr;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    padding: 15px 0 17px;

    &:first-child {
      border-top: 1px solid var(--special-light-grey-2);
    }
  }
}

.additional__item-title {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: var(--special-dark-grey-2);
  grid-row: 1 / 2;
  margin: 0;
  margin-bottom: 13px;

  @media (min-width: 768px) {
    padding-left: 38px;
  }

  @media (min-width: 1440px) {
    grid-column: 1 / 2;
    padding-left: 0;
    margin-bottom: 0;
  }
}

.additional__item-features {
  display: flex;
  justify-content: space-between;
  grid-row: 2 / 3;

  @media (min-width: 768px) {
    grid-row: 1 / 2;
    grid-column: 2 / 3;
    padding-right: 38px;
  }

  @media (min-width: 1440px) {
    grid-column: 2 / 3;
    padding-left: 0;
  }
}

.additional__item-feature {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  color: var(--special-dark-grey);
  margin: 0;
  margin-bottom: 15px;

  @media (min-width: 1440px) {
    margin-bottom: 0;
  }
}

.additional__item-price {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  color: var(--special-dark-grey);
  margin: 0;
}

.additional__item-button {
  padding-top: 10px;
  padding-bottom: 10px;

  @media (min-width: 768px) {
    grid-row: 2 / 3;
    grid-column: 1 / 3;
    margin: 0 38px;
  }

  @media (min-width: 1440px) {
    grid-row: 1 / 2;
    grid-column: 3 / 4;
    margin: 0;
  }
}
