.product {
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: min-content min-content auto;
  align-items: center;
  background-color: var(--basic-white);
  border-top: 1px solid var(--special-light-grey-2);
  padding: 19px 20px 25px;
  box-sizing: border-box;
  position: relative;
  z-index: 0;

  @media (min-width: 768px) {
    grid-template-columns: 1fr;
    justify-items: center;
    border: none;
    padding: 8px 36px 39px;

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 80%;
      background-color: var(--basic-grey);
      z-index: -1;
    }
  }

  @media (min-width: 1440px) {
    padding-right: 30px;
    padding-left: 35px;
  }
}

.product__title {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: var(--special-dark-grey-2);
  grid-row: 1 / 2;
  grid-column: 2 / 3;
  width: 60%;
  margin: 0;

  @media (min-width: 768px) {
    width: 154px;
    font-size: 20px;
    line-height: 24px;
    grid-row: 2 / 3;
    grid-column: 1 / 2;
    text-align: center;
  }
}

.product__link {
  text-decoration: none;
  grid-row: 1 / 3;
  grid-column: 1 / 2;

  @media (min-width: 768px) {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
  }
}

.product__image {
  @media (min-width: 768px) {
    width: 100%;
    min-height: 200px;
  }
}

.product__features {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: var(--special-dark-grey);
  grid-row: 2 / 3;
  grid-column: 2 / 3;
  margin-bottom: 22px;

  @media (min-width: 768px) {
    width: 251px;
    font-size: 16px;
    line-height: 16px;
    grid-row: 3 / 4;
    grid-column: 1 / 2;
    margin-bottom: 33px;
  }

  @media (min-width: 768px) {
    width: 180px;
  }
}

.product__features-item {
  display: flex;
  justify-content: space-between;

  @media (min-width: 768px) {
    border-bottom: 1px solid var(--special-grey);
    padding-top: 7px;
    padding-bottom: 9px;
  }
}

.product__button {
  grid-row: 3 / 4;
  grid-column: 1 / 3;
  padding-top: 10px;
  padding-bottom: 10px;

  @media (min-width: 768px) {
    grid-row: 4 / 5;
    grid-column: 1 / 2;
    width: 100%;
  }
}
